          </div>
      </div>
  </section>
        <div class="user-guide-custom d-none">

        </div>
        <div class="col-sm-12 col-md-12 col-lg-12 form-panel p-0" id="divMainData">
            <div class="form-content">
                <div class="border p-3">
                    <div class="row" id="theme-primary partition-full">
                        <div class="col-lg-4">
                            <div class="form-group">
                                <label>{{$t('RoleName')}}:<span class="text-danger">*</span></label>
                                <input id="RoleName" v-on:change="IsDuplicateRoleName" :placeholder="$t('RoleName')"
                                    class="form-control" autocomplete="false"
                                    v-bind:class="{ 'is-valid': validation.valid.RoleName, 'is-invalid': validation.invalid.RoleName }"
                                    v-model="RoleDetails.RoleName" />

                                <div class="invalid-feedback" v-if="validation.invalid.RoleName">{{
                                    validation.invalid.RoleName }}</div>
                                <div class="invalid-feedback-duplicate" v-if="isRoleDuplicate">{{
                                    validation.invalid.RoleDuplicate }}</div>
                            </div>
                        </div>
                        <div class="col-lg-4">
                            <div class="form-group">
                                <label>{{$t('Status')}}:<span class="text-danger">*</span></label>                                
                                <select class="form-control" v-model="RoleDetails.StatusId"
                                    v-bind:name="RoleDetails.StatusName">
                                    <option v-for="option in StatusList" 
                                        v-bind:selected="RoleDetails.StatusId == option.STATUS_ID"                                    
                                        v-bind:value="option.STATUS_ID">{{ option.STATUS }}
                                    </option>                                          
                                </select>
                            </div>
                        </div>


                        <div class="col-lg-4">
