                                <select class="form-control" v-on:change="onChangeUserType" v-model="RoleDetails.UserTypeId"
                                    v-bind:class="{ 'is-invalid': validation.invalid.UsreTypeId }">
                                    <option value="">Select</option>
                                    <option v-for="option in UserType"
                                        v-bind:selected="RoleDetails.StatusId == option.user_type_id"
                                        v-bind:value="option.user_type_id">{{ option.user_type_desc }}</option>
                                </select>
                                <div class="invalid-feedback" v-if="validation.invalid.UsreTypeId">{{
                                    validation.invalid.UsreTypeId }}</div>
                            </div>
                        </div>

                        <div class="clearfix"></div>
                     
                    </div>
                    <div class="row">
                        <div class="col-lg-12">
                            <div class="form-group">
                                <label>{{$t('DESCRIPTION')}}</label>:
                                <textarea v-bind:class="{ 'form-control': true }" maxlength="1000" rows="3" id="RoleDesc"
                                    name="RoleDesc" v-model="RoleDetails.RoleDesc" type="text"></textarea>

                            </div>
                        </div>
                    </div>
                    <div class="row">
                        <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 float-left">
                            <div class="container-fluid padding-t_10">
                                <div class="row">
                                    <div class="col-md-5 module-l-grey-clr div-click-event px-3">
                                        <div class="row">
                                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-3 ">
                                                <div class="heading-border">
                                                    <h5 class="h5">
                                                        <span>
                                                            {{$t('Modules')}}
                                                        </span>
                                                    </h5>
                                                </div>
                                                <div class="py-2 d-inline-block">
                                                    <span class="legends_medium colorboxsquare"></span> {{$t('MoreThanOnePrivilege')}}
                                                </div>
                                            </div>
                                        </div>
                                        <div class="row">
                                            <div class="col-10">
                                                <div class="form-group">
                                                    <input id="search" name="search" maxlength="50"
                                                        class="form-control search" type="text" value=""
                                                        :placeholder="$t('SearchByModuleName')" autocomplete="off"
                                                        v-model="SearchValue" v-on:change="SearchModuleName">
                                                </div>
                                            </div>
                                            <div class="col-2 float-left pl-0">
                                                <div class="background-0 text-left form-check-inline w-100">
                                                    <input type="hidden" id="hdnSearch" value="">
                                                    <a title="" id="Go" href="javascript:;"
                                                        class="btn btn-success w-100 mr-1 px-4 hlsearch"
                                                        data-toggle-tooltip="tooltip" data-original-title="Search"
                                                        v-on:click="SearchModuleName"><i class="fa fa-search"></i></a>
                                                </div>
                                            </div>
                                        </div>
                                        <div class="row">
                                            <div class="col-md-6 " id="divListing">
                                                <div class="mt-2" id="divscrollsetuptabs" style="max-height:500px">
                                                    <ul id="moduleList" role="tablist"
                                                        class="nav nav-pills flex-column left-tab responivecustomtab customli">
                                                        <li class="nav-item" v-for="(item, index) in ModuleList" :title="item.display_name ">
                                                          

                                                            <a href="'#tab-module-content'+index"
                                                                v-on:click="GetSubModule(item, index)"
                                                                v-bind:id="'tab-openactivities' + index" data-toggle="tab"
                                                                role="tab" aria-selected="false"
                                                                class="nav-link d-flex left-menu">
                                                                <div class="mr-auto">{{ item.display_name }}</div>
                                                                <div class="r-arrow"><i aria-hidden="true"
                                                                        class="fa fa-angle-right pl-2"></i></div>
                                                            </a>

                                                        </li>
                                                        <li v-if="ModuleList.length <= 0"  class="text-center text-danger pt-2">
                                                        
                                                                {{ $t('NoRecordfound') }}
                                                         
                                                        </li>
                                                    </ul>
                                                </div>
                                            </div>


                                            <div class="col-md-6" id="divsubModule">
                                                <div id="divscrollsetuptabsSubmodule" style="max-height:500px">
                                                    <ul id="subModuleList" role="tablist"
                                                        class="nav nav-pills flex-column left-tab mt-2 responivecustomtab customli">
                                                        <li class="nav-item d-flex" v-for="(item, index) in SubModuleList" :title="item.display_name ">
                                                            <a 
                                                                v-bind:id="'tab-submodule' + index"
                                                                v-on:click="SetPrivilegesBaseOnSubmodule(item, index)"
                                                                data-toggle="tab" role="tab" aria-selected="false"
                                                                class="nav-link left-menu mr-auto d-flex">

                                                                <div class="mr-auto">{{ item.display_name }}</div>
                                                                <div class="r-arrow"><i aria-hidden="true"
                                                                        class="fa fa-angle-right pl-2"></i></div>
                                                            </a>
                                                        </li>
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>


                                    </div>
                                    <div class="col-md-7">
                                        <div class="row">
                                            <div class="col-sm-12 col-md-12 col-lg-12 col-xl-12 mt-3 ">
                                                <div class="heading-border mb-3">
                                                    <h5 class="h5">
                                                        <span>
                                                            {{ $t('Privileges') }}
                                                        </span>
                                                    </h5>
                                                </div>

                                            </div>
                                        </div>


                                        <div class="tab-pane mb-0">
                                            <div id="" class="tab-content fsmtabcontrolMobileView">

                                                <div id="tab-module-content" role="tabpanel"
                                                    aria-labelledby="tab-submodule-content"
                                                    class="tab-pane fade border-0 listing p-0  show active">
                                                    <div class="table-responsive" v-if="isShowMainPrivilegeDiv">
                                                        <table id="tab-submodule-table"
                                                            class="table table-bordered dt-responsive nowrap mt-0">
                                                            <thead class="thead-bg">
                                                                <tr>
                                                                    <th class="text-center">
                                                                        <input id="" class="" value="" type="checkbox"
                                                                            v-on:click="SetCheckAllMainListPrivilege"
                                                                            v-model="CheckAll">
                                                                    </th>

                                                                    <th><span>Features</span></th>
                                                                    <th class="text-center"><span>List</span></th>
                                                                    <th class="text-center"><span>View</span></th>
                                                                    <th class="text-center"><span>Add</span></th>
                                                                    <th class="text-center"><span>Edit</span></th>
                                                                    <th class="text-center"><span>Delete</span></th>
                                                                    <th class="text-center"><span>BulkUpload</span></th>
                                                                    <th class="text-center"><span>Export</span></th>

                                                                </tr>
                                                            </thead>
                                                            <tbody>

                                                                <tr v-bind:class="{ 'd-none': item.isShow, '': item.isShow }"
                                                                    v-for="(item, index) in PrivilegeMainList">
                                                                    <td class="text-center"><input class="chkItems"
                                                                            v-on:click="selectSignleCheckboxMain(item)"
                                                                            v-model="item.checkitem" type="checkbox"></td>
                                                                    <td><span>{{ item.feature }}</span></td>
                                                                    <td v-bind:title="item.title_list" class="text-center">
                                                                        <span><input v-if="item.List != undefined"
                                                                                v-on:click="privilegeCheckboxCheck(item, 'List')"
                                                                                v-bind:value="item.List" v-model="item.List"
                                                                                type="checkbox" /></span>
                                                                    </td>
                                                                    <td v-bind:title="item.title_view" class="text-center">
                                                                        <span><input v-if="item.View != undefined"
                                                                                v-on:click="privilegeCheckboxCheck(item, 'View')"
                                                                                v-bind:value="item.View" v-model="item.View"
                                                                                type="checkbox" /></span>
                                                                    </td>
                                                                    <td v-bind:title="item.title_add" class="text-center">
                                                                        <span><input v-if="item.Add != undefined"
                                                                                v-on:click="privilegeCheckboxCheck(item, 'Add')"
                                                                                v-bind:value="item.Add" v-model="item.Add"
                                                                                type="checkbox" /></span>
                                                                    </td>
                                                                    <td v-bind:title="item.title_update"
                                                                        class="text-center"><span><input
                                                                                v-if="item.Update != undefined"
                                                                                v-on:click="privilegeCheckboxCheck(item, 'Update')"
                                                                                v-bind:value="item.Update"
                                                                                v-model="item.Update"
                                                                                type="checkbox" /></span></td>
                                                                    <td v-bind:title="item.title_delete"
                                                                        class="text-center"><span><input
                                                                                v-if="item.Delete != undefined"
                                                                                v-on:click="privilegeCheckboxCheck(item, 'Delete')"
                                                                                v-bind:value="item.Delete"
                                                                                v-model="item.Delete"
                                                                                type="checkbox" /></span></td>
                                                                    <td v-bind:title="item.title_import"
                                                                        class="text-center"><span><input
                                                                                v-if="item.Import != undefined"
                                                                                v-on:click="privilegeCheckboxCheck(item, 'Import')"
                                                                                v-bind:value="item.Import"
                                                                                v-model="item.Import"
                                                                                type="checkbox" /></span></td>
                                                                    <td v-bind:title="item.title_export"
                                                                        class="text-center"><span><input
                                                                                v-if="item.Export != undefined"
                                                                                v-on:click="privilegeCheckboxCheck(item, 'Export')"
                                                                                v-bind:value="item.Export"
                                                                                v-model="item.Export"
                                                                                type="checkbox" /></span></td>

                                                                </tr>
                                                            </tbody>
                                                        </table>
                                                    </div>
                                                </div>
                                            </div>

                                        </div>

                                        <div id="" class="tab-content fsmtabcontrolMobileView mt-4">

                                            <div id="tab-submodule-content" role="tabpanel"
                                                aria-labelledby="tab-submodule-content"
                                                class="tab-pane fade border-0 listing p-0 show active">

                                                <div class="table-responsive" v-if="isShowOtherPrivilegeDiv">

                                                    <table id="tab-submodule-table"
                                                        class="table table-bordered dt-responsive  nowrap mt-0">
                                                        <thead class="thead-bg">
                                                            <tr>
                                                                <th><span>Other Features </span></th>
                                                                <th class="text-center"><input id="" class=""
                                                                        v-on:click="SetCheckAllOtherPrivilege"
                                                                        v-model="CheckAllOtherList" value=""
                                                                        type="checkbox"></th>
                                                            </tr>
                                                        </thead>
                                                        <tbody>
                                                            <tr v-bind:class="{ 'd-none': item.isShow, '': item.isShow }"
                                                                v-for="(item, index) in PrivilegeOtherList">
                                                                <td><span>{{ item.privilege_desc }}</span></td>
                                                                <td class="text-center"><input class="chkItems"
                                                                        v-on:click="selectSignleCheckboxOtherPrivilege(item)"
                                                                        v-model="item.value" v-bind:value="item.value"
                                                                        type="checkbox"></td>

                                                            </tr>
                                                        </tbody>
                                                    </table>
                                                </div>

                                            </div>

                                        </div>
                                        <div id="divbutton" class="tab-content fsmtabcontrolMobileView mt-4"
                                            v-if="isShowOtherPrivilegeDiv || isShowMainPrivilegeDiv">
                                            <div class="row padding-t_10">
                                                <div class="col-sm-12 col-md-6 col-lg-6 text-left">
                                                    <span class="mandatory m-0">
                                                        {{ $t('MandatoryString') }}
                                                    </span>
                                                </div>
                                                <div class="col-sm-12 col-md-6 col-lg-6 text-right">
                                                    <div class="form-group m-0">
                                                        <a title="Save" v-on:click="SavePrivileges()" href="javascript:;"
                                                            class="btn btn-success text-white formbtn m-2" id="asave"
                                                            tabindex="5"><i class="fa fa-save pr-2"></i>Save</a>

                                                        <a title="Cancel"  v-on:click="GoToList"
                                                            id="Cancel" class="btn btn-danger text-white formbtn"
                                                            tabindex="6"><i class="fa fa-close pr-2"></i>Cancel</a>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>

        <div id="dinalreadyUserData"></div>
    </section>
</template>
<script>

import DataService from '../../services/DataService.js';
import loader from '../../components/Common/Loader.vue'


export default {
    data() {
        return {
            isLoading: false,
            ApiURL: "",
            ApiURLRoleDetail: "",
            ApiURLCheckDuplicate: "",
            ModuleList: [],
            ModuleListNonFiltered: [],
            AllPrivilegeFilter: [],
            SubModuleList: [],
            PrivilegesList: [],
            PrivilegeMainList: [],
            PrivilegeOtherList: [],
            PrivilegeAllList: [],
            RoleDetails: {  StatusId : 1001,
                UserTypeId : "",
                RoleName : "",
                RoleId : null},
            UserType: [],
            StatusList: [],
            ModuleId: null,
            SubModuleId: null,
            isShowOtherPrivilegeDiv: false,
            isShowMainPrivilegeDiv: false,
            RoleDesc: '',
            CheckAll: false,
            CheckAllOtherList: false,
            UserRoleDetails: [],
            GroupCode: '',
            isAllMainSelected: false,
            isAllOtherSelected: false,
            RoleName: '', // default values
            lastName: '',
            SearchValue: '',
            ddlDisabled: false,
            isUpdate:0, //vm.$route.params.id != '0' ? true : false,
            roleId:null,//(vm.$route.params.id==undefined)?0:vm.$route.params.id,
            isRoleDuplicate: false,
            savePrivilegesObject: [],
            validation: {
                invalid: {
                    RoleName: "",
                    UsreTypeId: null,
                    RoleDuplicate: null
                },
                valid: {
                }
            }
        }
    },
    created:async function () {
        var vm = this;
        await vm.GetStatusList();
         vm.GetRoleDetails();
         vm.GetUserType();        
         setTimeout(function () { vm.GetPrivilegesAllModuleSubmodule(); }, 3000);
    },
    methods: {
        FetchData() {

            var vm = this;
            vm.SubModuleList = [];
            vm.isLoading = true;
            var params = `moduleId=&userTypeId=${vm.RoleDetails.UserTypeId}`;
            DataService.GetModuleList(params)
                .then(function (response) {
                    if (response.data) {
                        if (response.data != undefined || response.data != null) {
                            vm.ModuleList = response.data.Data;
                            //debugger;
                            setTimeout(function () { vm.GetSubModule(vm.ModuleList[0], 0); }, 100);

                        }
                     vm.isLoading=false;
                    }
                });
        },
        SortPrivilegeList() {
            var vm = this;
            function compare(a, b) {
                if (a.sorting_order < b.sorting_order)
                    return -1;
                if (a.sorting_order > b.sorting_order)
                    return 1;
                return 0;
            }
            return vm.PrivilegeMainList.sort(compare);
        },
        SetCheckAllMainListPrivilege() {
           var vm = this;


            if (!vm.CheckAll) {

                $.each(vm.PrivilegeMainList, function (index, item) {
                    if (!item.isShow) {
                        vm.PrivilegeMainList[index].checkitem = 1;
                        item.List = item.List != null ? 1 : null;
                        item.Add = item.Add != null ? 1 : null;
                        item.Delete = item.Delete != null ? 1 : null;
                        item.Update = item.Update != null ? 1 : null;
                        item.View = item.View != null ? 1 : null;
                        item.Import = item.Import != null ? 1 : null;
                        item.Export = item.Export != null ? 1 : null;
                    }
                });
            }
            else {

                $.each(vm.PrivilegeMainList, function (index, item) {
                    if (!item.isShow) {
                        vm.PrivilegeMainList[index].checkitem = 0;
                        item.List = item.List != null ? 0 : null;
                        item.Add = item.Add != null ? 0 : null;
                        item.Delete = item.Delete != null ? 0 : null;
                        item.Update = item.Update != null ? 0 : null;
                        item.View = item.View != null ? 0 : null;
                        item.Import = item.Import != null ? 0 : null;
                        item.Export = item.Export != null ? 0 : null;

                        if (item.privilege_id_list != undefined) {
                            vm.MultiLevelPrivilegeUnCheck(item.privilege_id_list);
                        }
                        if (item.privilege_id_add != undefined) {
                            vm.MultiLevelPrivilegeUnCheck(item.privilege_id_add);
                        }
                        if (item.privilege_id_update != undefined) {
                            vm.MultiLevelPrivilegeUnCheck(item.privilege_id_update);
                        }
                        if (item.privilege_id_delete != undefined) {
                            vm.MultiLevelPrivilegeUnCheck(item.privilege_id_delete);
                        }
                        if (item.privilege_id_view != undefined) {
                            vm.MultiLevelPrivilegeUnCheck(item.privilege_id_view);
                        }
                        if (item.privilege_id_import != undefined) {
                            vm.MultiLevelPrivilegeUnCheck(item.privilege_id_import);
                        }
                        if (item.privilege_id_export != undefined) {
                            vm.MultiLevelPrivilegeUnCheck(item.privilege_id_export);
                        }
                    }
                });
            }

            vm.otherCheckAllButton();
        },
        GoToList(){
            var vm=this;
                vm.$router.push({
                        path: '/Roles/index'
                });
        },
        SetCheckAllOtherPrivilege() {
            //debugger
            var vm = this;

            if (!vm.CheckAllOtherList) {

                $.each(vm.PrivilegeOtherList, function (index, item) {
                    if (!item.isShow) {
                        vm.PrivilegeOtherList[index].value = 1;
                        if (vm.PrivilegeMainList.filter(v => (v.privilege_id == item.parent_id)).length > 0) {
                            vm.PrivilegeMainList.filter(v => (v.privilege_id == item.parent_id))[0].List = true;

                        }
                        vm.MultiLevelPrivilegeCheck(item.privilege_id);
                    }
                });
            }
            else {

                $.each(vm.PrivilegeOtherList, function (index, item) {
                    if (!item.isShow) {
                        vm.PrivilegeOtherList[index].value = 0;
                    }
                });
            }
        },
        SetPrivilegesBaseOnSubmodule(itm, indx) {
            // debugger
           var vm = this;
            vm.SubModuleId = itm.sub_module_id;
            vm.ModuleId = itm.module_id

            vm.isShowOtherPrivilegeDiv = true;
            vm.isShowMainPrivilegeDiv = true;
            //showLoader();
            if (vm.PrivilegeMainList.length > 0) {
                vm.CheckAll = true;
            }
            else {
                vm.CheckAll = false;
            }

            if (vm.PrivilegeOtherList.length > 0) {
                vm.CheckAllOtherList = true;
            }
            else {
                vm.CheckAllOtherList = false;
            }

            $.each(vm.PrivilegeMainList, function (index, item) {

                if (item.sub_module_id == itm.sub_module_id && item.module_id == itm.module_id) {
                    item.isShow = false;
                }
                else {
                    item.isShow = true;

                }
            });

            $.each(vm.PrivilegeOtherList, function (index, item) {

                if (item.sub_module_id == itm.sub_module_id && item.module_id == itm.module_id) {
                    item.isShow = false;
                }
                else {
                    item.isShow = true;
                }
            });

            vm.CheckAllCheckBoxMainListPrivilege();
            vm.CheckAllCheckBoxOtherPrivilege();

            vm.ShowHideMainAndOtherDiv();
            vm.refreshModuleList();
            //vm.CheckBoxBootstrap();
            //setTimeout("CheckBoxBootstrap()", 0);
            //  hideLoader();
        },
        refreshModuleList() {
            //debugger;
            var vm=this;
            var mainPrivilege = vm.PrivilegeMainList;
            vm.PrivilegeMainList = [];
            vm.PrivilegeMainList = mainPrivilege;

            var otherPrivilege = vm.PrivilegeOtherList;
            vm.PrivilegeOtherList = [];
            vm.PrivilegeOtherList = otherPrivilege;
        },
        GetPrivilegesAllModuleSubmodule() {
            //debugger;
         
            var vm = this;
            vm.isLoading=true;
            vm.isAllMainSelected = false;
            vm.PrivilegeOtherList = [];
            vm.PrivilegeMainList = [];
            
            if(vm.$route.params.id==undefined){
               // vm.RoleDetails={};
                vm.RoleDetails.StatusId = 1001;
                vm.RoleDetails.UserTypeId =vm.RoleDetails.UserTypeId>0?vm.RoleDetails.UserTypeId: "";
                vm.RoleDetails.RoleName =(vm.RoleDetails.RoleName!=null||vm.RoleDetails.RoleName!="")?vm.RoleDetails.RoleName : "";
                vm.RoleDetails.RoleId = null
            }
            if (vm.RoleDetails.RoleId == null) {
                vm.RoleDetails.RoleId = 0;
            }

            var params = `userTypeId=${vm.RoleDetails.UserTypeId}&moduleId=&subModuleId=&privilegeGroupCode=&roleId=${vm.RoleDetails.RoleId}`;
            DataService.GetPrivilegeList(params)
                .then(function (response) {
                    vm.PrivilegeAllList = response.data.PrivilegeVertical;
                    vm.PrivilegesList = response.data.PrivilegeVertical.filter(v => v.feature != null);
                    vm.PrivilegeOtherList = response.data.PrivilegeVertical.filter(v => v.feature == null);

                    vm.PrivilegeMainList = [];

                    const groupsPrivilegersMain = vm.PrivilegesList.reduce((groups, item) => {
                        const group = (groups[item.sub_module_id] || []);
                        group.push(item);
                        groups[item.sub_module_id] = group;
                        return groups;
                    }, {});
                    // debugger
                    $.each(groupsPrivilegersMain, function (index, item) {

                        var itemList = item.reduce((groups, item1) => {
                            const group = (groups[item1.feature] || []);
                            group.push(item1);
                            groups[item1.feature] = group;
                            return groups;
                        }, {});

                        $.each(itemList, function (index1, item12) {
                            var newObj = {};
                            $.each(item12, function (index1, item1) {
                                newObj.privilege_id = item1.privilege_id;

                                newObj.feature = item1.feature;
                                newObj.parent_id = item1.parent_id;
                                newObj.module_id = item1.module_id;
                                newObj.sub_module_id = item1.sub_module_id;
                                newObj.sorting_order = item1.sorting_order;

                                if (item1.action == 'Update') {
                                    newObj.Update = item1.value;
