        MultiLevelPrivilegeUnCheck(actionTypePrivilege_id) {

           var vm = this;


            var itemForChildPrivilegeId = vm.PrivilegeAllList.filter(v => v.parent_id == actionTypePrivilege_id);

            if (itemForChildPrivilegeId.length > 0) {

                $.each(itemForChildPrivilegeId, function (index1, item1) {
                    $.each(vm.PrivilegeMainList, function (index2, item2) {

                        if (item2.privilege_id_add == item1.privilege_id) {
                            item2.Add = false;
                        }
                        else if (item2.privilege_id_update == item1.privilege_id) {
                            item2.Update = false;
                        }
                        else if (item2.privilege_id_view == item1.privilege_id) {
                            item2.View = false;
                        }

                        else if (item2.privilege_id_delete == item1.privilege_id) {
                            item2.Delete = false;
                        }
                        else if (item2.privilege_id_list == item1.privilege_id) {
                            item2.List = false;
                        }
                        else if (item2.privilege_id_import == item1.privilege_id) {
                            item2.Import = false;
                        }
                        else if (item2.privilege_id_export == item1.privilege_id) {
                            item2.Export = false;
                        }
                        else {
                            var itemOther = vm.PrivilegeOtherList.filter(v => v.privilege_id == item1.privilege_id);
                            if (itemOther.length > 0) {
                                vm.PrivilegeOtherList.filter(v => v.privilege_id == item1.privilege_id)[0].value = false;
                            }
                        }



                    })
                    var itemForChildPrivilegeId = item1.privilege_id;
                    console.log(item1.privilege_id);
                    var itemForChildPrivilegeIdInner = vm.PrivilegeAllList.filter(v => v.parent_id == itemForChildPrivilegeId && v.parent_id != item1.parent_id);

                    start: while (itemForChildPrivilegeIdInner.length > 0) {
                        $.each(itemForChildPrivilegeIdInner, function (index3, item3) {
                            //alert(item3.parent_id);
                            $.each(vm.PrivilegeMainList, function (index4, item4) {

                                console.log('--child--');

                                if (item4.privilege_id_add == item3.privilege_id) {
                                    item4.Add = false;
                                }
                                else if (item4.privilege_id_update == item3.privilege_id) {
                                    item4.Update = false;
                                }
                                else if (item4.privilege_id_view == item3.privilege_id) {
                                    item4.View = false;
                                }
                                else if (item4.privilege_id_delete == item3.privilege_id) {
                                    item4.Delete = false;
                                }
                                else if (item4.privilege_id_list == item3.privilege_id) {
                                    item4.List = false;
                                }
                                else if (item4.privilege_id_import == item3.privilege_id) {
                                    item4.Import = false;
                                }
                                else if (item4.privilege_id_export == item3.privilege_id) {
                                    item4.Export = false;
                                }
                                else {
                                    var itemOther = vm.PrivilegeOtherList.filter(v => v.privilege_id == item3.privilege_id);
                                    if (itemOther.privilege_id == 246) {
                                        console.log(item4.parent_id)

                                    }
                                    if (itemOther.length > 0) {
                                        console.log('--inner child other--');
                                        vm.PrivilegeOtherList.filter(v => v.privilege_id == item3.privilege_id)[0].value = false;
                                    }
                                }


                            });
                            itemForChildPrivilegeIdInner = vm.PrivilegeAllList.filter(v => v.parent_id == item3.privilege_id && v.parent_id != item3.parent_id);
                        });
                        continue start

                    }
                });
            }
        },
        MultiLevelPrivilegeCheck(actionTypePrivilege_id) {
            // debugger
           var vm = this;

            var itemForParentId = vm.PrivilegeAllList.filter(v => v.privilege_id == actionTypePrivilege_id);
            if (itemForParentId.length > 0) {

                $.each(vm.PrivilegeMainList, function (index1, item1) {
                    vm.setCheckboxParentChild(item1, itemForParentId[0])

                    var itemForParentIdInner = vm.PrivilegeAllList.filter(v => v.privilege_id == itemForParentId[0].parent_id);
                    start: while (itemForParentIdInner.length > 0) {
                        $.each(vm.PrivilegeMainList, function (index1, item1) {
                            vm.setCheckboxParentChild(item1, itemForParentIdInner[0])
                        })
                        itemForParentIdInner = vm.PrivilegeAllList.filter(v => v.privilege_id == itemForParentIdInner[0].parent_id);
                        continue start
                    }

                });
            }
