                }];
                vm.isLoading=true;
                $.each(vm.PrivilegeMainList, function (index, item) {

                    if (item.List != undefined) {
                        var new_obj = {};
                        console.log(item.privilege_id_list + '  ' + item.List);
                        new_obj.privilege_id = item.privilege_id_list;
                        new_obj.value = Boolean(item.List);
                        vm.savePrivilegesObject.push(new_obj);
                    }

                    if (item.Add != undefined) {
                        var new_obj = {};
                        console.log(item.privilege_id_add + '  ' + item.Add);
                        new_obj.privilege_id = item.privilege_id_add;
                        new_obj.value = Boolean(item.Add);
                        vm.savePrivilegesObject.push(new_obj);
                    }

                    if (item.Update != undefined) {
                        var new_obj = {};
                        console.log(item.privilege_id_update + '   ' + item.Update);
                        new_obj.privilege_id = item.privilege_id_update;
                        new_obj.value = Boolean(item.Update);
                        vm.savePrivilegesObject.push(new_obj);
                    }

                    if (item.Delete != undefined) {
                        var new_obj = {};
                        console.log(item.privilege_id_delete + '  ' + item.Delete);
                        new_obj.privilege_id = item.privilege_id_delete;
                        new_obj.value = Boolean(item.Delete);
                        vm.savePrivilegesObject.push(new_obj);
                    }

                    if (item.View != undefined) {
                        var new_obj = {};
                        console.log(item.privilege_id_view + '  ' + item.View);
                        new_obj.privilege_id = item.privilege_id_view;
                        new_obj.value = Boolean(item.View);
                        vm.savePrivilegesObject.push(new_obj);
                    }
                    if (item.Import != undefined) {
                        var new_obj = {};
                        console.log(item.privilege_id_import + '  ' + item.Import);
                        new_obj.privilege_id = item.privilege_id_import;
                        new_obj.value = Boolean(item.Import);
                        vm.savePrivilegesObject.push(new_obj);
                    }
                    if (item.Export != undefined) {
                        var new_obj = {};
                        console.log(item.privilege_id_export + '  ' + item.Export);
                        new_obj.privilege_id = item.privilege_id_export;
                        new_obj.value = Boolean(item.Export);
                        vm.savePrivilegesObject.push(new_obj);
                    }
                })

                if(vm.PrivilegeOtherList.length>0){
                    $.each(vm.PrivilegeOtherList, function (index, item) {
                        var new_obj = {};
                            if(item!=0){
                                new_obj.privilege_id = item.privilege_id;
                                new_obj.value = Boolean(item.value);
                                vm.savePrivilegesObject.push(new_obj);
                            }
                      
                    })
                }

                //debugger
                var postJSON = {
                    PrivilegeVertical: vm.savePrivilegesObject,
                    RoleDetails: vm.UserRoleDetails
                };
                var objectPostString = JSON.stringify(postJSON);

                DataService.SavePrivileges(objectPostString).then(response => {
                    if (response.data != null) {
                        vm.ShowAlert(vm.$t("RecordsSavedSuccessfully"), "success", vm.$t('Alert'));
                        location.href = "/Roles/index";
                        vm.submited = false;
                    }
                    //setTimeout(function () { vm.CheckBoxBootstrap() }, 100);
                });
            }
        },
        SearchModuleName() {
            //debugger;
            var vm = this;
            if ((vm.ModuleListNonFiltered.length <= 0) && vm.SearchValue.length > 0) {
                vm.ModuleListNonFiltered = vm.ModuleList;
            }
            else if ((vm.ModuleListNonFiltered.length > 0) && vm.SearchValue.length <= 0) {
                vm.ModuleList = vm.ModuleListNonFiltered;
            }
            //  if (vm.SearchValue.length > 0) {
            vm.ModuleList = this.ModuleList.filter(v => {
                return v.display_name.toLowerCase().indexOf(vm.SearchValue.toLowerCase()) > -1
            });

            if (vm.ModuleList.length <= 0) {
                vm.SubModuleList = [];
            }
            if (vm.ModuleList.length > 0) {
                setTimeout(function () { vm.GetSubModule(vm.ModuleList[0], 0); }, 100);
            }
            else {
                return false;
            }

            //  }
        }

    }
}
</script>
